* {
  box-sizing: border-box;
}

body {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  color: $black;
  margin: 0;
  line-height: 1;
}

sup {
  align-self: flex-start;
  margin-top: 4px;
}

.green, .red, .yellow {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: flex;
  align-self: center;
  margin-right: 10px;
}


p {
  line-height: 20px;
  font-size: 14px;
  font-weight: 300;
}

.container {
  display: flex;
  flex-direction: row;
  //justify-content: space-between;
  margin-top: 33px;
  width: 100%;
  //overflow-x: hidden;
}
aside {
  width: 300px;
  background: $white;
  min-height: 550px;
  height: 97vh;
  position: fixed;
  //flex-grow: 2;
  left: 0;
  padding: 33px 0px 33px 65px;
  border-right: 1px solid $gray;
  display: flex;
  flex-direction: column;
  .logo {
    position: relative;
    &:before {
      position: absolute;
      content: '';
      width: 25px;
      height: 30px;
      top: 0;
      left: -34px;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      @include sprite;
      background-position: -6px -10px;
    }
    span {
      font-weight: 500;
      font-size: 14px;
      color: $black;
      text-transform: uppercase;
    }
    a {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .menu {
    margin: 56px 0 0;
    padding-right: 50px;
    padding-bottom: 34px;
    border-bottom: 1px solid $gray;
    display: flex;
    flex-direction: column;
    a {
      font-weight: 400;
      font-size: 16px;
      color: $black;
      line-height: 20px;
      margin-bottom: 29px;
      text-decoration: none;
      &:hover {
        @include animate-hover;
        color: $gray-link;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &.active {
        padding-left: 20px;
        border-left: 2px solid $red;
      }
    }
  }
  .link-map {
    margin-top: 30px;
    a {
      color: $gray-link;
      text-decoration: none;
    }
  }
  .social-links {
    margin-top: auto;
    span {
      font-weight: 400;
      font-size: 14px;
      color: $black;
    }
    .flax-conteiner {
      display: flex;
      justify-content: space-between;
      padding-right: 60px;
      margin-top: 16px;
      .item {
        width: 37px;
        height: 37px;
        border-radius: 50%;
        @include sprite;
        &.fb {
          background-position: 0 -79px;
        }
        &.vk {
          background-position: -64px -79px;
        }
        &.lj {
          background-position: -128px -79px;
        }
      }
    }
  }
}
.content {
  //width: 1220px;
  padding-left: 130px;
  padding-top: 33px;
  //margin-left: 300px;
  position: fixed;
  left: 300px;
  bottom: 0;
  top: 0;
  right: 0;
  overflow-x: auto;
  .back-link {
    margin-bottom: 23px;
    a {
      text-decoration: none;
      font-weight: 500;
      color: $black;
      display: flex;
      align-items: center;
      &:hover {
        @include animate-hover;
        color: $gray-link;
      }
      &:before {
        content: '';
        width: 26px;
        height: 14px;
        @include sprite;
        margin-right: 16px;
        background-position: -64px -17px;
      }
    }
  }
  .banner {
    padding: 102px 40px 75px 45px;
    background: $blue url(../img/banner-bg.jpg) center no-repeat;
    background-size: cover;
    display: flex;
    justify-content: space-between;
    .banner-left-b {
      min-width: 780px;
      display: flex;
      flex-direction: column;

    }
    .banner-right-b {
      min-width: 196px;
      margin-top: auto;
      span {
        width: 90px;
        display: flex;
        font-size: 14px;
        line-height: 20px;
        font-weight: 400;
        color: $white;
        opacity: .5;
        margin-bottom: 15px;
      }
      a {
        color: $white;
        display: flex;
        align-items: center;
        font-weight: 300;
        font-size: 12px;
        opacity: .5;
        &:after {
          width: 18px;
          height: 9px;
          content: '';
          @include sprite;
          background-position: -64px -40px;
          margin-left: 10px;
        }
      }
    }
    .title {
      font-weight: 300;
      font-size: 52px;
      margin: 0;
      text-transform: uppercase;
      color: $white;
      line-height: 1;
      margin-bottom: 5px;
    }
    .name {
      font-weight: 700;
      font-size: 82px;
      color: $white;
      margin: 0 0 46px 0;
    }
    .result {
      color: $white;
      font-size: 36px;
      font-weight: 700;
      margin: 0 0 13px 0;
    }
    .adress {
      color: $white;
      font-size: 24px;
      font-weight: 400;
      display: flex;
      align-items: center;
      line-height: 24px;
      margin-bottom: 13px;
      &:before {
        content: '';
        width: 15px;
        height: 19px;
        @include sprite;
        background-position: -120px -15px;
        margin-left: 2px;
        margin-right: 9px;
      }
    }
    .val {
      font-size: 14px;
      color: $white;
      font-weight: 400;
      opacity: .5;
      padding-left: 27px;
    }
  }
  .today {
    margin-top: 75px;
    .title {
      font-weight: 700;
      font-size: 52px;
      color: $black;
      margin: 0 0 30px;
    }
    .station {
      display: flex;
      margin: 0;
      height: 50px;
      width: 100%;
      padding-left: 75px;
      color: $white;
      font-size: 14px;
      font-weight: 400;
      background: $blue;
      align-items: center;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        left: 32px;
        top: 0;
        bottom: 0;
        width: 19px;
        height: 23px;
        @include sprite;
        background-position: -175px -9px;
        margin-top: auto;
        margin-bottom: auto;
      }
    }
    .semi-title {
      font-size: 14px;
      font-weight: 400;
      color: $black;
    }
    .status {
      width: 100%;
      height: 123px;
      background: $gray-block;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding:0 47px 0 43px;

      .left-b {
        h3 {
          margin: 0;
          font-size: 24px;
          font-weight: 400;
          margin-bottom: 10px;
          text-transform: uppercase;
        }
        p {
          color: $gray-text;
          font-size: 14px;
          font-weight: 300;
          margin: 0;
        }
      }
      .right-b {
        display: flex;
        ul {
          display: flex;
          list-style-type: none;
          a {
            color: #9d9e9f;
            font-weight: 500;
            font-size: 14px;
            display: flex;
            height: 30px;
            align-items: center;
            text-decoration: none;
            position: relative;
            @include animate-hover;
            &.active {
              color: $black;
              &:before {
                content: '';
                width: 100%;
                height: 2px;
                position: absolute;
                top: 0;
                left: 0;
                background: $red;
              }
            }
          }
        }
        .menu-l {
          border-right: 1px solid #9d9e9f;
          padding-right: 28px;
          li {
            padding-right: 10px;
            padding-left: 10px;
          }
        }
        .menu-r {
          padding-left: calc(37px - 15px);
          li {
            padding-left: 15px;
            padding-right: 15px;
          }
        }
      }
    }
    .card-wrapper {
      &.active {
        display: flex;
      }
      display: none;
      flex-wrap: wrap;
      width: 100%;
      margin-top: 8px;
    }
    .card-td {
      width: calc(100%/4 - 6px);
      height: 291px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border-radius: 3px;
      border: 1px solid $gray-card;
      margin-right: 8px;
      margin-bottom: 8px;
      &:nth-child(4n) {
        margin-right: 0;
      }
      .green {
        background: $green;
      }
      .red {
        background: $red;
      }
      .yellow {
        background: $yellow;
      }
      .arrowTop, .arrowBot {
        width: 9px;
        height: 9px;
        @include sprite;
        margin-top: auto;
        margin-bottom: 6px;
        margin-left: 9px;
        margin-right: 7px;
      }
      .arrowBot {
        background-position: -280px -23px;
      }
      .arrowTop {
        background-position: -324px -27px;
      }
      .result {
        font-size: 14px;
        font-weight: 400;
        opacity: .6;
        margin-top: auto;
        margin-bottom: 3px;
      }
      .card-td__head {
        padding: 0 20px;
        height: 68px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        span {
          font-weight: 700;
          font-size: 14px;
          color: $black;
          opacity: .6;
        }
        a {
          display: flex;
          width: 16px;
          height: 16px;
          @include sprite;
          background-position: -224px -15px;
        }
      }
      .card-td__body {
        display: flex;
        align-items: center;
        flex-direction: column;
        p {
          margin: 0;
          font-weight: 500;
          color: $black;
          font-size: 18px;
        }
        h3 {
          display: flex;
          font-weight: 700;
          font-size: 52px;
          color: $black;
          margin: 17px 0 0 0;
          height: 44px;
          align-items: center;
          span {
            display: flex;
          }
        }
      }
      .card-td__foot {
        height: 102px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        p {
          margin: 0;
          font-weight: 400;
          color: $black;
          font-size: 14px;
        }
        span {
          font-size: 14px;
          font-weight: 400;
          opacity: .6;
          color: $black;
        }
      }
    }
    .btn-card {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 42px;
      width: 134px;
      background: #f2f5f6;
      color: $black;
      font-weight: 500;
      font-size: 14px;
      margin: 15px auto;
      text-decoration: none;
      @include animate-hover;
      &:hover {
        color: $white;
        background: #6fb3e4;
      }
    }
  }
  .dinamic {
    .title {
      font-size: 52px;
      font-weight: 700;
      color: $black;
    }
    .text {
      padding-left: 77px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        width: 52px;
        height: 52px;
        @include sprite;
        background-position: -366px 0;
        top: 0;
        left: 0;
      }
      .sami-title {
        font-size: 18px;
        font-weight: 400;
        color: $black;
      }
      p {
        font-weight: 300;
        color: $black;
        font-size: 14px;
      }
    }
    .status {
      width: 100%;
      height: 159px;
      background: $gray-block;
      display: flex;
      flex-direction: column;
      padding:0 47px 0 43px;
      .head {
        display: flex;
        align-items: center;
        height: calc(159px - 63px);
        width: 100%;
        justify-content: space-between;
      }
      .left-b {
        h3 {
          margin: 0;
          font-size: 24px;
          font-weight: 400;
          margin-bottom: 10px;
          text-transform: uppercase;
        }
        span {
          color: $gray-text;
          font-size: 14px;
          font-weight: 300;
          margin: 0;
          text-transform: none;
        }
      }
      .right-b {
        display: flex;
        ul {
          display: flex;
          list-style-type: none;
          a {
            color: #9d9e9f;
            font-weight: 500;
            font-size: 14px;
            display: flex;
            height: 30px;
            align-items: center;
            text-decoration: none;
            position: relative;
            &.active {
              color: $black;
              &:before {
                content: '';
                width: 100%;
                height: 2px;
                position: absolute;
                top: 0;
                left: 0;
                background: $red;
              }
            }
          }
        }
        .menu-l {
          border-right: 1px solid #9d9e9f;
          padding-right: 28px;
          li {
            padding-right: 10px;
            padding-left: 10px;
          }
        }
        .menu-r {
          padding-left: calc(37px - 15px);
          li {
            padding-left: 15px;
            padding-right: 15px;
          }
        }
      }
      .body {
        width: 100%;
        display: flex;
        justify-content: space-between;
        a {
          font-weight: 300;
          color: #848485;
          font-size: 14px;
          text-decoration: none;
          padding: 16px;
          @include animate-hover;
          &:hover{
            @include box-shadow;
            background: $red;
            border-radius: 5px;
            color: $white;
          }
          &.active {
            @include box-shadow;
            background: $red;
            border-radius: 5px;
            color: $white;
          }
        }
      }
    }
    .grafic {
      height: 380px;
      width: 100%;
      display: none;
      &.active {
        display: block;
      }
    }
    .period {
      margin-bottom: 70px;
      display: flex;
      margin-top: 20px;
      a {
        padding: 15px;
        text-decoration: none;
        font-size: 16px;
        color: $black;
        margin-right: 5px;
        @include animate-hover;
        &:hover {
          @include box-shadow;
          background: $red;
          border-radius: 5px;
          color: $white;
        }
        &.active {
          @include box-shadow;
          background: $red;
          border-radius: 5px;
          color: $white;
        }
      }
    }
  }
  .carusel {
    .title {
      font-size: 52px;
      font-weight: 700;
      color: $black;
      margin: 0 0 14px 0;
    }
    .owl-carousel {
      margin-bottom: 50px;
    }
    p {
      font-size: 18px;
      font-weight: 300;
      color: $black;
      margin-bottom: 30px;
      margin-top: 0;
    }
  }
  footer {
    margin-top: 190px;
    padding-top: 42px;
    padding-bottom: 55px;
    border-top: 1px solid #b2b2b2;
    display: flex;
    justify-content: space-between;
    .okul {
      height: 34px;
      width: 268px;
      background: #000;
      color: $white;
      display: flex;
      align-items: center;
      text-decoration: none;
      justify-content: center;
      border-radius: 3px;
      margin-right: 15px;
      @include animate-hover;
      &:before {
        content: '';
        width: 29px;
        height: 18px;
        @include sprite;
        background-position: -587px -21px;
        margin-right: 16px;
        font-size: 14px;
        font-weight: 400;

      }
      &:hover {
        text-decoration: none;
        @include box-shadow;
      }
    }
    .site-map {
      text-decoration: none;
      height: 34px;
      width: 162px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: $black;
      font-weight: 400;
      border-radius: 3px;
      border: 1px solid $black;
      @include animate-hover;
      &:before {
        content: '';
        width: 22px;
        height: 19px;
        @include sprite;
        background-position: -655px -17px;
        margin-right: 14px;
      }
      &:hover {
        text-decoration: none;
        @include box-shadow;
      }
    }
    .buttons {
      display: flex;
      margin-bottom: 17px;
    }
    .left-b {
      p {
        margin-top: 10px;
        &:first-child {
          margin-bottom: 19px;
        }
      }
    }
    .right-b {
      width: 538px;
      p {
        font-size: 13px;
        font-weight: 300;
      }
    }
    p {
      font-size: 14px;
      font-weight: 500;
      color: $black;
      margin: 0;
    }
    a {
      font-size: 14px;
      font-weight: 400;
      color: $black;
      text-decoration: none;
      @include animate-hover;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}

.owl-carousel .owl-item {
  .item {
    img {
      width: 100%;
      height: auto;
      border-radius: 5px;
    }
  }
}
.owl-theme .owl-nav {
  margin: 0;
  position: absolute;
  top: -56px;
  right: 0;
  width: 81px;
  display: flex;
  justify-content: space-between;
}
.owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next {
  @include sprite;
  padding: 0;
  margin: 0;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  //opacity: 1 !important;
  transition: box-shadow .2s;
  border: 1px solid $blue;
}
.owl-carousel .owl-nav .owl-prev {
  background-position: -467px -10px;
  &:hover {
    @include sprite;
    background-position: -467px -10px;
    @include box-shadow;
  }
}
.owl-carousel .owl-nav .owl-next {
  background-position: -511px -10px;
  &:hover {
    @include sprite;
    background-position: -511px -10px;
    @include box-shadow;
  }
}
.owl-theme .owl-dots .owl-dot span {
  margin-top: 0;
  margin-bottom: 0;
  background: transparent;
  border: 1px solid $blue;
  &:hover {
    background: $blue;
  }
}
.owl-theme .owl-dots .owl-dot.active span {
  background: $blue;
}
.owl-theme .owl-dots {
  margin-top: 24px;
}

@media (max-width: 1545px) {
  .side-bar {
    width: 280px;
    //width: 20vw;
    z-index: 1000;
  }
  .content {
    position: fixed;
    left: 280px;
    padding-left: 40px;
    //width: 80vw;
    margin-left: 0;
    padding-right: 30px;
  }
  .content .banner .banner-left-b {
    min-width: 0;
  }
  .content .dinamic .status {
    padding-left: 25px;
    padding-right: 25px;
  }
  .content .dinamic .status .body a {
    padding: 14px;
  }
}

@media (max-width: 1279px) {
  .content .banner {
    flex-direction: column;
  }
  .content .banner .banner-right-b {
    margin-top: 25px;
  }
  .content .dinamic .status .right-b .menu-r {
    padding: 0;
  }
  .content .dinamic .status .right-b .menu-l {
    padding: 0;
  }
  //.content .dinamic .status .body a {
  //  padding: 1vw;
  //}
  .content .dinamic .status .head {
    flex-direction: column;
  }
  .content .dinamic .status .body {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .content .today .status {
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    height: auto;
  }
}