$black: #000;
$white: #fff;
$gray: #eaeaea;
$gray-link: #b8b8b8;
$gray-block: #f7f8f9;
$gray-text: #797979;
$gray-card: #d5dde0;
$red: #ea312a;
$blue: #2c82cb;
$green: #67d000;
$yellow: #ffd200;



$icon: #d6d034; //вставить иконки вместо этого цвета

@mixin sprite {
  background: transparent url(../img/sprite.png) no-repeat;
}
@mixin animate-hover {
  transition: all .2s;
}

@mixin box-shadow {
  box-shadow: -3px 3px 12px 0px rgba(0,0,0,0.2);
}